import React from "react";
import "./sass/mine-link.scss";
import {Redirect} from "react-router-dom";
export default class LoginLink extends React.Component {
    constructor (props){
        super(props);
        this.state={
            jumptomine:""
        }
    }
    jumpToMine = () => {
        this.setState({jumptomine:"/mine"});
    }
    render () {
        if(this.state.jumptomine) {
            return (
                <Redirect to={this.state.jumptomine}></Redirect>
            )
        }
        return (
            <p className="link" onClick={this.jumpToMine}>我的</p>
        );
    }
}