import React from "react";
import "./sass/search-result.scss";
import { reqFindBook } from "../../api/index";
import {Badge } from "antd-mobile";
import {Redirect} from "react-router-dom";
import {connect} from "react-redux";
import {changeBookDetail} from "../../redux/action";
class SearchResult extends React.Component{
    constructor(props){
        super(props);
        this.state={
            books:[]
        }
    }
    async componentWillMount(){
        let response=await reqFindBook({bookname:this.props.bookname});
        this.setState({books:response.data.data});
    }
    jumpToDetailPage(item){
        localStorage.setItem("clickBack","false");
        item.count=1;
        this.props.changeBookDetail(item);
        this.setState({jumpToDetail:true});
    }
    render(){
        if(this.state.jumpToDetail){
            return (
                 <Redirect to="/bookdetails"></Redirect>
            )
        }
        return (
            <div className="search-result">
                {
                    this.state.books.length>0?
                    // this.state.books.map((item,index)=>{
                    //     return (
                    //         <div className="find-res" key={index} onClick={this.jumpToDetailPage.bind(this,item)}>
                    //         <Badge text={item.booktype} style={{padding: '0 3px', backgroundColor: '#21b68a', borderRadius: 2,left:"-1rem",top:"-1rem" }} />
                    //         <div className="img-name-price">
                    //         <img className="img" src={item.img}/>
                    //         <div className="name-price">
                    //             <p className="bookname">{item.bookname}</p>
                    //             <p className="price">￥{item.bookprice}</p>
                    //         </div>
                    //         </div>
                    //         {/* <p className={this.state.showall?"open":"close"} ></p>
                    //         <p className="show-all"></p> */}
                    //         <p className="intro" >{item.bookintro}</p>
                    //         </div> 
                    //     )
                    // }):
                    
                        this.state.books.map((item,index)=>{
                            return (
                                <div className="card" key={index} onClick={this.jumpToDetailPage.bind(this,item)}>
                                    <img src={item.img} alt=""/>
                                    <p className="novel-name">{item.bookname}</p>
                                    <p className="novel-price">￥{item.bookprice}</p>
                                </div>
                            )
                        })
                    :
                    <p className="empty">未搜索到相关书籍</p>
                }
            </div>
        );
    }
}
export default connect(
    (state)=>({bookdetail:state.bookdetail}),
    {changeBookDetail}
)(SearchResult);