import { reqRegister,reqLogin } from "../api/index";
import {
    AUTH_SUCCESS,
    ERROR_MSG,
    CHANGE_MAIN_CONTENT,
    CHANGE_SYS_AREA,
    UPLOAD_IMAGE_URL,
    ADD_TO_CHART,
    BOOK_DETAIL,
} from "./action-type";
//分发注册的同步action
const authSuccess = (user) => ({type: AUTH_SUCCESS,data:user});
const errorMsg = (msg) => ({type:ERROR_MSG,data:msg});
//分发注册的异步action
export const register= ({username,pass,tel})=> {
    return async dispatch =>{
        const response = await reqRegister({username,pass,tel});
        const result = response.data;
        if(result.code===0){
            dispatch(authSuccess({...result.data,msg:result.msg,redirectTo:"/"}));
        }
        else {
            dispatch(errorMsg({...result.data,msg:result.msg}));
        }
    }
}
//更改main页content内容的同步action
export const changeMainConent=(isActive)=>({type:CHANGE_MAIN_CONTENT,data:isActive});
//分发登录的异步action
export const login=(user)=>{
    console.log("user.username",user.username)
    if(!user.username) {
        return authSuccess(user);
    }
    return async dispatch=>{
        const response=await reqLogin(user);
        const result=response.data;
        if(result.code===0){
            dispatch(authSuccess({...result.data,msg:result.msg,redirectTo:"/"}));
        }
        else {
            dispatch(errorMsg({...result.data,msg:result.msg}));
        }
    }
}
//更改sys页功能面板的同步action
export const changeSysArea=(showArea)=>({type:CHANGE_SYS_AREA,data:showArea});
//分发上传图片url的同步action
export const uploadimg=(url)=>({type:UPLOAD_IMAGE_URL,data:url});
//分发添加购物车的同步action
export const addToChart=(all)=>({type:ADD_TO_CHART,data:all});
//分发查看商品详情的同步action
export const changeBookDetail=(item)=>({type:BOOK_DETAIL,data:item});