import React from "react";
import "./sass/buy.scss";
import { connect } from "react-redux";
import { Redirect } from "react-router-dom";
import { reqBuyBook, reqGetUser } from "../../api/index";
import Cookies from "js-cookie";
import { NavBar, Icon, Toast } from 'antd-mobile';
import { district } from 'antd-mobile-demo-data';
class Buy extends React.Component {
    constructor(props) {
        super(props);
        this.state = {
            toSuccess: false,
            toFail: false,
            toMain: false,
            chart: [],
            total: 0,
            addr: "",
            toLogin: false,
            toMine: false,
        }
    }
    async componentWillMount() {
        // if (!Cookies.get("token")) {
        //     Toast.info("请先登录！");
        //     await this.setState({ toLogin: true });
        //     return;
        // }
        let chart, total;
        if (!this.props.bookdetail.item.img) {
            chart = JSON.parse(localStorage.getItem("chart"));
            total = chart.reduce((prev, current) => {
                console.log(prev, current.bookprice)
                return prev + parseFloat(current.bookprice);
            }, 0);
        } else {
            chart = [this.props.bookdetail.item];
            total = chart[0].bookprice;
        }
        total = total.toFixed(1);
        this.setState({ chart, total });
        let _id = Cookies.get("token").match(/j:"(.*?)"/)[1];
        const response = await reqGetUser({ _id });
        let addr = response.data.data.addr;
        if (addr.length < 1) {
            Toast.info("请先选择收货地址！", 1.5);
            await this.setState({ toMine: true });
            return;
        }
        let firstLevelAddr = district.filter(item => (item.value === addr[0]))[0];
        let secondLevelAddr = firstLevelAddr.children.filter(item => (item.value === addr[1]))[0];
        let thirdLevelAddr = secondLevelAddr.children.filter(item => (item.value === addr[2]))[0];
        let absoluteAddr = `${firstLevelAddr.label} ${secondLevelAddr.label} ${thirdLevelAddr.label} ${addr[3]}`;
        this.setState({ addr: absoluteAddr });
    }
    back() {
        localStorage.setItem("clickBack", "true");
        let hashRoutes = JSON.parse(localStorage.getItem("hashRoutes"));
        let hash = hashRoutes.pop();
        localStorage.setItem("hashRoutes", JSON.stringify(hashRoutes));
        window.location.href = `http://localhost:3000/#${hash}`;
    }
    componentWillUnmount() {
        if (localStorage.getItem("clickBack") !== "true") {
            let hashRoutes = localStorage.getItem("hashRoutes") ? JSON.parse(localStorage.getItem("hashRoutes")) : [];
            hashRoutes.push("/buy");
            localStorage.setItem("hashRoutes", JSON.stringify(hashRoutes));
        }

    }
    async buyEvent() {
        //    let _ids= this.state.chart.reduce((prev,current)=>{
        //         console.log("prev",prev)
        //         return [...prev,current._id];
        //     },[])
        localStorage.setItem("clickBack", "false");
        let chart = this.state.chart;
        const _id = Cookies.get("token").match(/j:"(.*?)"/)[1];
        const response = await reqBuyBook({ _id, books: chart });
        const result = response.data;
        if (result.code === 0) {
            console.log(0)
            this.setState({ toSuccess: true });
        } else {
            this.setState({ toFail: false });
        }
        localStorage.setItem("chart", JSON.stringify([]));

    }
    cancelEvent() {
        localStorage.setItem("clickBack", "false");
        this.setState({ toMain: true });
    }
    render() {
        const _this = this;
       if (this.state.toSuccess) {
            return (
                <Redirect to="/success"></Redirect>
            );
        }
        else if (this.state.toMain) {
            return (
                <Redirect to="/"></Redirect>
            );
        }
        else if (this.state.toFail) {
            return (
                <Redirect to="/fail"></Redirect>
            );
        }
        else if (this.state.toMine) {
            return (
                <Redirect to="/mine"></Redirect>
            );
        }
        return (
            <>
                <NavBar
                    mode="dark"
                    icon={<Icon type="left" />}
                    onLeftClick={_this.back.bind(this)}
                    className="nav"
                >图书商城</NavBar>
                <div className="buy">
                    <p className="title">请核对信息</p>
                    {
                        this.state.chart.map((item, index) => {
                            return (
                                <div className="card" key={index}>
                                    <div className="box">
                                    <img src={item.img} alt="" />
                                    <div className="name-price">
                                        <div className="bookname">{item.bookname}</div>
                                        <div className="price">￥{item.bookprice}</div>
                                    </div>
                                    </div>
                                    
                                    <div className="num">数量：{item.count}</div>
                                </div>
                                //     <div className="card" key={index}>
                                //     <div className="img-name-price">
                                //         <img src={item.img} alt=""/>
                                //         <div className="bookname-price">
                                //             <div className="bookname">{item.bookname}</div>
                                //             <div className="price">￥{item.bookprice}</div>
                                //         </div>
                                //     </div>      
                                //     <div className="add-time">{item.date}</div>
                                //     <div className="num">数量：{item.count}</div>
                                // </div>
                            );
                        })
                    }
                    <div className="addr-box">
                        <p className="check-addr">收货地址</p>
                        <p className="addr-descripe">{this.state.addr}</p>
                    </div>
                    <div className="total-price">总价：￥{this.state.total}</div>
                    <footer>
                        <button onClick={this.buyEvent.bind(this)}>确&nbsp;&nbsp;定</button>
                        <button onClick={this.cancelEvent.bind(this)}>取&nbsp;&nbsp;消</button>
                    </footer>
                </div>
            </>
        );
    }
}
export default connect(
    (state) => ({ bookdetail: state.bookdetail })
)(Buy);