import React from "react";
import "./sass/favorite-books.scss";
import {reqGetPeopleBuys,reqFindBook} from "../../api/index";
import {Redirect} from "react-router-dom";
import {connect} from "react-redux";
import {changeBookDetail} from "../../redux/action";
class FavouriteBooks extends React.Component {
    constructor (props) {
        super(props);
        this.state = {
            favoriteBooks:[],
            jumpToDetail:false,
        }
    }
   async componentDidMount () {
       const response = await reqGetPeopleBuys();
       const booknames=response.data.data;
       const favoriteBooks=[];
       for(let bookname of booknames){
           const response2=await reqFindBook({bookname});
           let book=response2.data.data;
           favoriteBooks.push(book[0]);
       }
       console.log("favoriteBooks",favoriteBooks)
       this.setState({favoriteBooks});
    }
    jumpToDetailPage(item){
        localStorage.setItem("clickBack","false");
        item.count=1;
        this.props.changeBookDetail(item);
        this.setState({jumpToDetail:true});
    }
     render () {
        if(this.state.jumpToDetail){
            return (
                 <Redirect to="/bookdetails"></Redirect>
            )
        }
        return (
            <div className="books">
                <p className="people-like">大家都喜欢</p>
                <div className="book-container">
                    {
                       // this.state.favoriteBooks.map(item => <img key={item._id} src={item.img} className="book"/>)
                        this.state.favoriteBooks.map((item,index)=>{
                            return (
                                <div className="card" key={index} onClick={this.jumpToDetailPage.bind(this,item)}>
                                    <img src={item.img} alt=""/>
                                    <p className="novel-name"
                                         style={{
                                            "overflow": "hidden",
                                            "textOverflow": "ellipsis",
                                            "WebkitLineClamp": "2",
                                            "WebkitBoxOrient": "vertical"
                                        }}
                                    >{item.bookname}</p>
                                </div>
                            )
                        })
                    }   
                </div>
           </div>
        );
    }
}
export default connect(
    (state)=>({bookdetail:state.bookdetail}),
    {changeBookDetail}
)(FavouriteBooks);