import React from "react";
import "./sass/chart.scss";
import {connect} from "react-redux";
// import {reqGetUser} from "../../api/index";
// import Cookies from "js-cookie";
import {Redirect} from "react-router-dom";
import {changeBookDetail} from "../../redux/action";
import { NavBar,Icon  } from 'antd-mobile';
class Chart extends React.Component{
    constructor(props){
        super(props);
        this.state={
            chart:[],
            toBuy:false,
            backToMain:false,
        }
    }
    async componentWillMount(){
        // const userid=Cookies.get("token").match(/j:"(.*?)"/)[1];
        // const response=await reqGetUser({_id:userid});
        // const chart=response.data.data.all;
        let chart = JSON.parse(localStorage.getItem("chart"))||[];
        this.setState({chart});
    }
    async buyEvent(){
        localStorage.setItem("clickBack","false");
        this.props.changeBookDetail({});
        this.setState({toBuy:true});
    }
    componentWillUnmount(){
        if(localStorage.getItem("clickBack")!=="true"){
            let hashRoutes=localStorage.getItem("hashRoutes")?JSON.parse(localStorage.getItem("hashRoutes")):[];
            hashRoutes.push("/chart");
            localStorage.setItem("hashRoutes",JSON.stringify(hashRoutes));
        }
    }
    backBefore(){
        localStorage.setItem("clickBack","true");
        let hashRoutes=JSON.parse(localStorage.getItem("hashRoutes"));
        console.log("hashRoutes",hashRoutes)
        let hash=hashRoutes.pop();
        localStorage.setItem("hashRoutes",JSON.stringify(hashRoutes));
        window.location.href=`http://localhost:3000/#${hash}`;
    }
    back(){
        localStorage.setItem("clickBack","false");
        this.setState({backToMain:true});
    }
    render(){
        const _this=this;
        if(this.state.backToMain){
            return (
                <Redirect to="/"></Redirect>
            );
        }
        else if(this.state.toBuy){
            return (
                <Redirect to="/buy"></Redirect>
            )
        }
        return (
            <>
            <NavBar
                mode="dark"
                icon={<Icon type="left" />}
                onLeftClick={_this.backBefore.bind(this)}
                className="nav"
            >图书商城</NavBar>
            <div className="chart">
                {
                    this.state.chart.map((item,index)=>{
                        return (
                            <div className="card" key={index}>
                            <div className="img-name-price">
                                <img src={item.img} alt=""/>
                                <div className="bookname-price">
                                    <div className="bookname">{item.bookname}</div>
                                    <div className="price">￥{item.bookprice}</div>
                                </div>
                            </div>      
                            <div className="add-time">{item.date}</div>
                            <div className="num">数量：{item.count}</div>
                        </div>
                        );
                    })
                }
                <footer>
                    <button onClick={this.buyEvent.bind(this)}>立即购买</button>
                    <button onClick={this.back.bind(this)}>返回</button>
                </footer>
            </div>
            </>
        );
    }
}
export default connect(
    (state)=>({chart:state.chart,bookdetail:state.bookdetail}),
    {changeBookDetail}
)(Chart);