import React from "react";
import "./sass/strive.scss";
import { reqFindBook } from "../../api/index";
import {Redirect} from "react-router-dom";
import {connect} from "react-redux";
import {changeBookDetail} from "../../redux/action";
class Strive extends React.Component{
    constructor(props){
        super(props);
        this.state={
            res:[],
            jumpToDetail:false,
        }
    }
    async componentWillMount(){
        const response=await reqFindBook({"booktype":"励志"});
        const result=response.data;
        await this.setState({res:result.data});
    }
    jumpToDetailPage(item){
        localStorage.setItem("clickBack","false");
        item.count=1;
        this.props.changeBookDetail(item);
        this.setState({jumpToDetail:true});
    }
    render(){
        if(this.state.jumpToDetail){
            return (
                 <Redirect to="/bookdetails"></Redirect>
            )
        }
        return (
            <div className="strive">
                {
                    this.state.res.map((item,index)=>{
                        return (
                            <div className="card" key={index} onClick={this.jumpToDetailPage.bind(this,item)}>
                                <img src={item.img} alt=""/>
                                <p className="strive-name"
                                style={{
                                    "overflow": "hidden",
                                    "textOverflow": "ellipsis",
                                    "display": "-webkit-box",
                                    "WebkitLineClamp": "2",
                                    "WebkitBoxOrient": "vertical"
                                }}
                                >{item.bookname}</p>
                                <p className="strive-price">￥{item.bookprice}</p>
                            </div>
                        )
                    })
                }
            </div>
        );
    }
}
export default connect(
    (state)=>({bookdetail:state.bookdetail}),
    {changeBookDetail}
)(Strive);