import React from 'react';
import "./sass/main.scss";
import LoginLink from "../../components/login-link/login-link.jsx";
import RegisterLink from "../../components/register-link/register-link.jsx";
import MineLink from "../../components/mine-link/mine-link";
import FavouriteBooks from "../../components/favorite-books/favorite-books.jsx";
import Content from "../../components/content/content";
import {connect} from "react-redux";
import {changeMainConent, login} from "../../redux/action";
import {Redirect} from "react-router-dom";
import SearchResult from "../../components/search-result/search-result";
import Cookies from "js-cookie";
import { NavBar,Icon  } from 'antd-mobile';
import { 
    Carousel, 
    WingBlank,
} from "antd-mobile";
class Main extends React.Component {
    constructor (props) {
        super(props);
        this.state = {
            swipeImgHeight: "6rem",
            segments: [
               {id:"novel",text:"小说"},
               {id:"literature",text:"文学"},
               {id:"comics",text:"动漫"},
               {id:"strive",text:"励志"},
               {id:"famousbook",text:"名著"}
            ],
            toChart:"",
            toLogin:false,
            goodsNum:0,
            bookname:"",
            showSearchRes: false,
        }
        this.swipeImgs = [];
        for(let i = 0;i < 3;i++) {
            this.swipeImgs.push(
                {
                 id:`swipeImg${i+1}`,
                 image:require(`./images/item${i+1}.jpg`)
                }
                );
        }
    }
    back(){
        localStorage.setItem("clickBack","true");
        let hashRoutes=JSON.parse(localStorage.getItem("hashRoutes"));
        console.log("hashRoutes",hashRoutes)
        let hash=hashRoutes.pop();
        localStorage.setItem("hashRoutes",JSON.stringify(hashRoutes));
        window.location.href=`http://localhost:3000/#${hash}`;
    }
    async search(){
        await this.setState({showSearchRes:true});
    }
    compoDidMount(){
        console.log('swipeImgs', this.swipeImgs);
    }
    componentWillMount(){
        let chart=localStorage.getItem("chart");
        let goodsNum=0;
        if(chart){
            JSON.parse(chart).forEach(item=>goodsNum+=item.count);
        }        
        this.setState({goodsNum});
    }
    componentWillUnmount(){
        if(localStorage.getItem("clickBack")!=="true"){
            let hashRoutes=localStorage.getItem("hashRoutes")?JSON.parse(localStorage.getItem("hashRoutes")):[];
            hashRoutes.push("/");
            localStorage.setItem("hashRoutes",JSON.stringify(hashRoutes));
        }
    }
    async selectContent(key){
        localStorage.setItem("clickBack","false");
        await this.props.changeMainConent(key);
    }
    jumpToChart=()=>{
        localStorage.setItem("clickBack","false");
        this.setState({toChart:"/chart"});
    }
    exitLogin () {
        localStorage.setItem("clickBack","false");
        Cookies.remove("token");
        login({username:"", pass:"", tel:"", msg:"", redirectTo: '', });
        this.setState({toLogin:true});
    }
   async changeBookname(e){
        if(e.target.value===""){
            await this.setState({showSearchRes:false});
            return;
        }
        this.setState({bookname:e.target.value});
    }
    render() {
        const _this=this;
        if(this.state.toChart){
            return (
                <Redirect to={this.state.toChart}></Redirect>
            );
        }
        if(this.state.toLogin){
            return (
                <Redirect to="/login"></Redirect>
            ); 
        }
        return (
            <>
            <NavBar
                mode="dark"
                icon={<Icon type="left" />}
                onLeftClick={_this.back.bind(this)}
                className="nav"
            >图书商城</NavBar>
            <div className="main">
                <header>
                    <div className="icon"></div>
                    <div className="search-box">
                        <input type="text" placeholder="搜索" onChange={this.changeBookname.bind(this)}/>
                        <i className="searchIcon" onClick={this.search.bind(this)}></i>
                   </div>
                        {
                            document.cookie.includes("token")?
                             (<div className="login-register-mine">
                                <MineLink></MineLink>
                                /
                                <p className="exitLogin" onClick={this.exitLogin.bind(this)}>退出</p>
                             </div>
                             
                             )
                            :(<div className="login-register-mine">
                                 <LoginLink></LoginLink>
                                 /
                                 <RegisterLink></RegisterLink>
                             </div>
                            )
                        }
                </header>
            {
                    this.state.showSearchRes?
                (
                    <SearchResult bookname={this.state.bookname}></SearchResult>
                ):
                (
                    <>
                    <div className="my-swiper">
                        <WingBlank>
                            <Carousel autoplay={true} infinite dots={true}>
                                {
                                    this.swipeImgs.map(item => <img key={item.id} src={item.image} style={{height: this.state.swipeImgHeight}}/>)
                                }
                            </Carousel>
                        </WingBlank>
                    </div>
                    <FavouriteBooks />
                    <div className="segments">
                     {this.state.segments.map(item=><p key={item.id} className={this.props.main.isActive===item.id?"segment active":"segment"} onClick={this.selectContent.bind(this,item.id)}>{item.text}</p>)}
                    </div>
                    <div className="content">
                        <Content></Content> 
                    </div>
                    <footer>
                    <div className="chart-bottom" onClick={this.jumpToChart}>
                    <p className="goods-num">{this.state.goodsNum}</p>
                    </div>
                </footer>
                    </>
                )
            }
            </div>
            </>


        );
    }
}
export default connect(
    (state)=>({main:state.main,chart:state.chart}),
    {changeMainConent}
)(Main);