import React from "react";
import "./sass/book-details.scss";
import { connect } from "react-redux";
import {Redirect} from "react-router-dom";
import {addToChart} from "../../redux/action";
import { NavBar,Icon, Toast  } from 'antd-mobile';
import Cookies from "js-cookie";
class BookDetails extends React.Component {
    constructor(props) {
        super(props);
        this.state = {
            res: {},
            toMain:false,
            toBuy:false,
            toLogin: false,
        }
    }
    async componentWillMount() {
        await this.setState({ res: this.props.bookdetail.item });    
    }
    componentWillUnmount(){
        if(localStorage.getItem("clickBack")!=="true"){
            let hashRoutes=localStorage.getItem("hashRoutes")?JSON.parse(localStorage.getItem("hashRoutes")):[];
            hashRoutes.push("/bookdetails");
            localStorage.setItem("hashRoutes",JSON.stringify(hashRoutes)); 
        }
    }
    getTime(){
        let now=new Date();
        let year=now.getFullYear();
        let month=now.getMonth()+1;
        let day=now.getDate();
        let hour=now.getHours();
        let minute=now.getMinutes();
        let second=now.getSeconds();
        return `${year}-${month.toString().padStart(2,0)}-${day.toString().padStart(2,0)}-${hour.toString().padStart(2,0)}-${minute.toString().padStart(2,0)}-${second.toString().padStart(2,0)}`;
    }
    async jumpToMain(){
        if(!Cookies.get('token')) {
            await this.setState({toLogin: true});
            Toast.info('请先登录！');
            return;
        }
        localStorage.setItem("clickBack","false");
        let chart = localStorage.getItem("chart")?JSON.parse(localStorage.getItem("chart")):[];
        //let all=this.props.chart.all;
        let index=chart.findIndex(item=>item._id===this.state.res._id);
        if(index===-1){
            let obj=this.state.res;
            obj.count=1;
            obj.date=this.getTime();
            chart.push(obj);

        } else {
            chart[index].count=chart[index].count+1;
            chart[index].date=this.getTime();
        }
        localStorage.setItem("chart",JSON.stringify(chart));
        this.setState({toMain:true});        
    }
    back(){
        localStorage.setItem("clickBack","true");
        let hashRoutes=JSON.parse(localStorage.getItem("hashRoutes"));
        console.log("hashRoutes",hashRoutes)
        let hash=hashRoutes.pop();
        localStorage.setItem("hashRoutes",JSON.stringify(hashRoutes));
        window.location.href=`http://localhost:3000/#${hash}`;
    }
    async buy(){
        if(!Cookies.get('token')) {
            await this.setState({toLogin: true});
            Toast.info('请先登录！');
            return;
        }
        localStorage.setItem("clickBack","false");
        this.setState({toBuy:true});
    }
    render() {
        const _this=this;
        if(this.state.toMain){
            return (
                <Redirect to="/"></Redirect>
            )
        }
        else if(this.state.toBuy){
            return (
                <Redirect to="/buy"></Redirect>
            )
        } else if(this.state.toLogin) {
            return (
                <Redirect to="/login"></Redirect>
            )
        }
        return (
            <>
            <NavBar
                mode="dark"
                icon={<Icon type="left" />}
                onLeftClick={_this.back.bind(_this)}
                className="nav"
            >图书商城</NavBar>
            <div className="book-details">
                <div className="img-price">
                    <img src={this.state.res.img} alt=""/>
                    <div className="name-price">
                     <div className="bookname">{this.state.res.bookname}</div>
                     <div className="price">￥{this.state.res.bookprice}</div>
                    </div>
                    
                </div>
                <div className="introduce">
                    详情：{this.state.res.bookintro}
                </div>
                <footer>
                    <button className="add-chart" onClick={this.jumpToMain.bind(this)}>加入购物车</button>
                    <button className="buy-immediate" onClick={this.buy.bind(this)}>立即购买</button>
                </footer>
            </div>
            </>
        );
    }
}
export default connect(
    (state) => ({ chart: state.chart,bookdetail:state.bookdetail }),
    {addToChart}
)(BookDetails);