import React from "react";
import "./sass/mine.scss";
import {reqGetUser,reqSaveUser,reqSaveAddr} from "../../api/index";
import Cookies from "js-cookie";
import {uploadimg} from "../../redux/action";
import UploadImg from "../../components/upload-img/upload-img";
import {Redirect} from "react-router-dom";
import { Picker, List, Toast} from "antd-mobile";
import {connect} from "react-redux";
import { district } from 'antd-mobile-demo-data';
import { NavBar,Icon  } from 'antd-mobile';
class Mine extends React.Component {
    constructor (props) {   
        super(props);
        this.state = {
            visible: false,
            res:{},
            userid:"",
            toHistory:false,
            toModifyPass: false,
            pickerValue: [],
            isReadOnly: true,
            specialAddr:"",
        }
    }
    // myclick = () => {
    //     this.setState((prevstate)=>{
    //         return {visible:!prevstate.visible};
    //     })
    // }
    // okHandler = (v) => {
    //     this.setState((prevstate) => {
    //         return {visible:!prevstate.visible,pickerValue: v}
    //     });
    // }
    async changeHead(v){
        let res=this.state.res;
        res.userpic=v;
        this.setState({res});
        this.props.uploadimg(v);
        await reqSaveUser({_id:this.state.userid,newdata:res});
    }
    back(){
        localStorage.setItem("clickBack","true");
        let hashRoutes=JSON.parse(localStorage.getItem("hashRoutes"));
        console.log("hashRoutes",hashRoutes)
        let hash=hashRoutes.pop();
        localStorage.setItem("hashRoutes",JSON.stringify(hashRoutes));
        window.location.href=`http://localhost:3000/#${hash}`;
    }
    async componentWillMount(){
        const userid=Cookies.get("token").match(/j:"(.*?)"/)[1];
        const response=await reqGetUser({_id:userid});
        const result=response.data;
        this.setState({res:result.data,userid});
        this.props.uploadimg(result.data.userpic);
        
    }
    componentWillUnmount(){
        if(localStorage.getItem("clickBack")!=="true"){
            let hashRoutes=localStorage.getItem("hashRoutes")?JSON.parse(localStorage.getItem("hashRoutes")):[];
            hashRoutes.push("/mine");
            localStorage.setItem("hashRoutes",JSON.stringify(hashRoutes));
        }
        
    }
    toHistoryPage(e){
        localStorage.setItem("clickBack","false");
        this.setState({toHistory:true});
    }
    jumpToModifyPass(e){
        localStorage.setItem("clickBack","false");
        this.setState({toModifyPass:true});
    }
   async editAddr(e){
      await  this.setState({specialAddr:e.target.value});
    }
   async startEdit(e){
        await this.setState({isReadOnly:false});
        this.refs.input.focus();
    }
    async saveAddr(){
        if(this.state.pickerValue.length<1){
            return Toast.info("请先选择收获地址");
        }
        else if(!this.state.specialAddr){
            return Toast.info("请先输入地址");
        }
        await this.setState({isReadOnly:true});
        let addr=this.state.pickerValue;
        addr.push(this.state.specialAddr);
        const _id=Cookies.get("token").match(/j:"(.*?)"/)[1];
        const response=await reqSaveAddr({_id,addr});
        Toast.info(response.data.msg);
    }
    render () {
        const _this=this;
        if(this.state.toHistory){
            return (
                <Redirect to="/historyorders"></Redirect>
            );
        }
        if(this.state.toModifyPass){
            return (
                <Redirect to="/ModifyPass"></Redirect>
            );
        }
        return (
            <>
            <NavBar
                mode="dark"
                icon={<Icon type="left" />}
                onLeftClick={_this.back.bind(this)}
                className="nav"
            >图书商城</NavBar>
            <div className="mine">
                <header>
                <UploadImg changeUrl={this.changeHead.bind(this)}></UploadImg>
                    <div className="nickname">
                        {this.state.res.username}
                    </div>
                </header>
                <List style={{ backgroundColor: 'white' }} className="picker-list">
                </List>
                <div className="modify-pass-head" onClick={this.jumpToModifyPass.bind(this)}>修改密码</div>
                <div className="history-orders-head" onClick={this.toHistoryPage.bind(this)}>历史订单</div>
                <Picker
                    title="选择地区"
                    extra="请选择(可选)"
                    data={district}
                    value={this.state.pickerValue}
                    onChange={v => this.setState({ pickerValue: v })}
                    onOk={(v,e) => {this.setState({ pickerValue: v });}}
                   >
                 <List.Item
                 className="select-region"
                 >请选择收货地址</List.Item>
                </Picker>
                <div className="addr">
                    <textarea cols={47} type="text" ref="input" onChange={this.editAddr.bind(this)} placeholder="请填写具体的收货地址" readOnly={this.state.isReadOnly}/>
                    <b className="icon" onClick={this.startEdit.bind(this)}>编辑</b>
                    <b className="save" onClick={this.saveAddr.bind(this)}>保存</b>
                </div>           

            </div>
            </>
        );
    }
}
export default connect(
    (state)=>({uploadImg:state.uploadImg}),
    {uploadimg}
)(Mine);